* {
   margin: 0px;
   padding: 0px;
   box-sizing: border-box;
   box-shadow: none;
   outline: none;
}

html {
   scroll-behavior: smooth;
}

body {
   overflow-x: hidden;
   color: #17252a;
   background-color: #feffff;
   text-align: center;
   font-weight: normal;
   font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", "Poppins", sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}

code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
}

.logo {
   text-transform: uppercase;
   letter-spacing: 3px;
   font-size: 12px;
   font-weight: bold;
   color: #feffff;
   z-index: 30;
}

nav {
   display: flex;
   flex-direction: row;
   justify-content: space-around;
   align-items: center;
   min-height: 8vh;
   background-color: #17252a;
   position: -webkit-sticky;
   position: sticky;
   top: 0;
   right: 0;
   width: 100vw;
   z-index: 20;
   font-family: "Poppins", sans-serif;
}

.nav-links {
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   align-items: center;
   background-color: #17252a;
   width: 20%;
}

ul {
   list-style-type: none;
}

.burger-menu span {
   background-color: #feffff;
   width: 40px;
   height: 2px;
   color: #feffff;
   border: double;
   border-width: 1.5px;
}

.burger-menu {
   display: none;
   z-index: 30;
   -webkit-transform: scale(0.8);
           transform: scale(0.8);
}

.burger-menu.visible {
   visibility: visible;
   pointer-events: auto;
   cursor: pointer;
}

.burger-menu.hidden {
   visibility: hidden;
   pointer-events: none;
}

.black-overlay {
   position: fixed;
   pointer-events: none;
   top: 0;
   left: 0;
   height: 100vh;
   width: 100vw;
   background-color: black;
   opacity: 0;
   z-index: 19;
}

@-webkit-keyframes black-animation {
   0% {
      opacity: 0;
   }
   100% {
      opacity: 0.5;
   }
}

@keyframes black-animation {
   0% {
      opacity: 0;
   }
   100% {
      opacity: 0.5;
   }
}

@-webkit-keyframes black-animation-rev {
   0% {
      opacity: 0.5;
   }
   100% {
      opacity: 0;
   }
}

@keyframes black-animation-rev {
   0% {
      opacity: 0.5;
   }
   100% {
      opacity: 0;
   }
}

.black-active {
   -webkit-animation: black-animation 0.5s ease-in forwards;
           animation: black-animation 0.5s ease-in forwards;
}
.black-rev {
   -webkit-animation: black-animation-rev 0.5s ease-in forwards;
           animation: black-animation-rev 0.5s ease-in forwards;
}

.nav-links button {
   text-decoration: none;
   color: #feffff;
   background: none;
   border: none;
   font-size: 17px;
   font-weight: normal;
   z-index: 30;
}

@media screen and (max-width: 990px) {
   .nav-links {
      flex-direction: column;
      justify-content: space-evenly;
      position: fixed;
      z-index: 10;
      right: -300px;
      top: 0;
      height: 100vh;
      width: 300px;
   }
   .burger-menu {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      height: 50px;
      width: 50px;
   }
   .nav-links button {
      font-size: 20px;
   }
   .logo {
      position: relative;
      left: 20px;
   }
}

@media screen and (max-width: 600px) {
   .nav-links {
      width: 200px;
   }
}

.nav-active {
   -webkit-animation: sidebar-animation 0.5s ease-in forwards;
           animation: sidebar-animation 0.5s ease-in forwards;
}

.nav-rev {
   -webkit-animation: sidebar-animation-rev 0.5s ease-in forwards;
           animation: sidebar-animation-rev 0.5s ease-in forwards;
}

@-webkit-keyframes sidebar-animation {
   0% {
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
   }
   100% {
      -webkit-transform: translateX(-300px);
              transform: translateX(-300px);
   }
}

@keyframes sidebar-animation {
   0% {
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
   }
   100% {
      -webkit-transform: translateX(-300px);
              transform: translateX(-300px);
   }
}

@-webkit-keyframes sidebar-animation-rev {
   0% {
      -webkit-transform: translateX(-300px);
              transform: translateX(-300px);
   }
   100% {
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
   }
}

@keyframes sidebar-animation-rev {
   0% {
      -webkit-transform: translateX(-300px);
              transform: translateX(-300px);
   }
   100% {
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
   }
}

@-webkit-keyframes nav-link-animation {
   100% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      color: white;
      letter-spacing: 1px;
   }
}

@keyframes nav-link-animation {
   100% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      color: white;
      letter-spacing: 1px;
   }
}

@-webkit-keyframes nav-link-animation-reverse {
   0% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      color: white;
      letter-spacing: 1px;
   }
   100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      color: #feffff;
   }
}

@keyframes nav-link-animation-reverse {
   0% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      color: white;
      letter-spacing: 1px;
   }
