      color: #feffff;
   }
}

.nav-link-hover {
   -webkit-animation: nav-link-animation 0.15s linear forwards;
           animation: nav-link-animation 0.15s linear forwards;
   cursor: pointer;
}
.nav-link-rev {
   -webkit-animation: nav-link-animation-reverse 0.15s linear forwards;
           animation: nav-link-animation-reverse 0.15s linear forwards;
}

@-webkit-keyframes spin {
   0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
   100% {
      -webkit-transform: translateY(-13px) rotate(-135deg);
              transform: translateY(-13px) rotate(-135deg);
   }
}

@keyframes spin {
   0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
   100% {
      -webkit-transform: translateY(-13px) rotate(-135deg);
              transform: translateY(-13px) rotate(-135deg);
   }
}

@-webkit-keyframes spin-reverse {
   0% {
      -webkit-transform: translateY(-13px) rotate(-135deg);
              transform: translateY(-13px) rotate(-135deg);
   }
   100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
}

@keyframes spin-reverse {
   0% {
      -webkit-transform: translateY(-13px) rotate(-135deg);
              transform: translateY(-13px) rotate(-135deg);
   }
   100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
}

@-webkit-keyframes disappear {
   0% {
      -webkit-transform: translate(0px, 0px);
              transform: translate(0px, 0px);
      opacity: 1;
   }
   100% {
      -webkit-transform: translate(40px, 0px);
              transform: translate(40px, 0px);
      opacity: 0;
   }
}

@keyframes disappear {
   0% {
      -webkit-transform: translate(0px, 0px);
              transform: translate(0px, 0px);
      opacity: 1;
   }
   100% {
      -webkit-transform: translate(40px, 0px);
              transform: translate(40px, 0px);
      opacity: 0;
   }
}

@-webkit-keyframes disappear-reverse {
   0% {
      -webkit-transform: translate(40px, 0px);
              transform: translate(40px, 0px);
      opacity: 0;
   }
   100% {
      -webkit-transform: translate(0px, 0px);
              transform: translate(0px, 0px);
      opacity: 1;
   }
}

@keyframes disappear-reverse {
   0% {
      -webkit-transform: translate(40px, 0px);
              transform: translate(40px, 0px);
      opacity: 0;
   }
   100% {
      -webkit-transform: translate(0px, 0px);
              transform: translate(0px, 0px);
      opacity: 1;
   }
}

@-webkit-keyframes spin1 {
   0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
   100% {
      -webkit-transform: translateY(13px) rotate(135deg);
              transform: translateY(13px) rotate(135deg);
   }
}

@keyframes spin1 {
   0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
   100% {
      -webkit-transform: translateY(13px) rotate(135deg);
              transform: translateY(13px) rotate(135deg);
   }
}

@-webkit-keyframes spin1-reverse {
   0% {
      -webkit-transform: translateY(13px) rotate(135deg);
              transform: translateY(13px) rotate(135deg);
   }
   100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
}

@keyframes spin1-reverse {
   0% {
      -webkit-transform: translateY(13px) rotate(135deg);
              transform: translateY(13px) rotate(135deg);
   }
   100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
}

.line-1.animate {
   -webkit-animation: spin1 0.5s ease-in forwards;
           animation: spin1 0.5s ease-in forwards;
}
.line-2.animate {
   -webkit-animation: disappear 0.5s ease-in forwards;
           animation: disappear 0.5s ease-in forwards;
}
.line-3.animate {
   -webkit-animation: spin 0.5s ease-in forwards;
           animation: spin 0.5s ease-in forwards;
}
.line-1.animate-rev {
   -webkit-animation: spin1-reverse 0.5s ease-in forwards;
           animation: spin1-reverse 0.5s ease-in forwards;
}
.line-2.animate-rev {
   -webkit-animation: disappear-reverse 0.5s ease-in forwards;
           animation: disappear-reverse 0.5s ease-in forwards;
}
.line-3.animate-rev {
   -webkit-animation: spin-reverse 0.5s ease-in forwards;
           animation: spin-reverse 0.5s ease-in forwards;
}

